import { FC, useState } from 'react'

import { Portal } from '@mui/material'

import { PATH } from '../../../constants/routePaths.enum'
import { useAppSelector } from '../../../hooks/reduxHooks'
import { useNavigateNoUpdates } from '../../../utils/routerUtils'
import { DeletePack } from '../Modals/DeletePack/DeletePack'
import { EditPack } from '../Modals/EditPack/EditPack'

import s from './PackActionsMenu.module.scss'

type PackActionsMenuType = {
  openToLeft?: boolean
  packID: string
  packName: string
  packIsEmpty: boolean
}

export const PackActionsMenu: FC<PackActionsMenuType> = ({
  openToLeft,
  packID,
  packName,
  packIsEmpty,
}) => {
  const packDeckCover = useAppSelector<string | null>(state => state.cards.cardsData.packDeckCover)
  const navigate = useNavigateNoUpdates()
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const menuSheet = isMenuOpen ? `${openToLeft ? s.openMenuLeft : s.openMenuRight}` : ''
  const menuItemStyle = isMenuOpen ? `${openToLeft ? s.openItemLeft : s.openItemRight}` : ''
  const listStyle = `${s.menuItem} ${menuItemStyle}`
  const [openModals, setOpenModals] = useState<number[]>([0, 0])
  const showModals = openModals.reduce((a, b) => a + b, 0)
  const sendData = {
    id: packID,
    name: packName,
    deckCover: packDeckCover || '',
  }
  const portalStyle = isMenuOpen ? s.portal : ''
  const portalStyleBg = showModals ? s.portalBgColor : ''

  const handleToogleMenu = () => setIsMenuOpen(!isMenuOpen)

  const handleClose = () => {
    setOpenModals([0, 0])
    setIsMenuOpen(false)
    document.body.style.overflow = 'unset'
  }

  const handleLearn = () => {
    if (packIsEmpty) {
      navigate(PATH.LEARN + `/${packID}`)
    }
  }

  const handleEdit = () => {
    setOpenModals([1, 0])
    document.body.style.overflow = 'hidden'
  }

  const handleDelete = () => {
    setOpenModals([0, 1])
    document.body.style.overflow = 'hidden'
  }

  return (
    <>
      <Portal>
        <div className={portalStyle || portalStyleBg} onClick={handleClose}>
          {!!showModals && (
            <div className={showModals ? s.modalsContainer : ''}>
              {!!openModals[0] && <EditPack data={sendData} activeModal={handleClose} />}
              {!!openModals[1] && <DeletePack packData={sendData} activeModal={handleClose} />}
            </div>
          )}
        </div>
      </Portal>

      <div className={`${s.menuContainer} ${menuSheet}`} onClick={handleToogleMenu}>
        {isMenuOpen && (
          <ul className={s.menu} onClick={handleToogleMenu}>
            <li
              className={`${listStyle} ${packIsEmpty ? '' : s.disableLearn}`}
              onClick={handleLearn}
            ></li>
            <li className={listStyle} onClick={handleEdit}></li>
            <li className={listStyle} onClick={handleDelete}></li>
          </ul>
        )}
      </div>
    </>
  )
}
