import React, { FC, useState, memo, useRef, MutableRefObject, useEffect } from 'react'

import { useEffectAfterMount } from '../../../hooks/useEffectAfterMount'
import { SelectType, SortValuesType } from '../SortItems/SortItems'

import s from './CustomSelect.module.scss'

type CustomSelectType = {
  initValue: SortValuesType
  reset?: boolean
  options: SelectType<SortValuesType>[]
  selectedOption: (option: SortValuesType) => void
}

export const CustomSelect: FC<CustomSelectType> = memo(
  ({ initValue, reset, options, selectedOption }) => {
    const initSelect = options.filter(el => el.id === initValue)[0].label
    const [selected, setSelected] = useState(initSelect)
    const [isOpen, setIsOpen] = useState(false)
    const selectRef = useRef() as MutableRefObject<HTMLDivElement>

    const handleClickOption = (e: React.MouseEvent<HTMLLIElement>, id: SortValuesType) => {
      setSelected(e.currentTarget.textContent!)
      selectedOption(id)
    }

    const handleToggleSelect = () => setIsOpen(prev => !prev)

    useEffectAfterMount(() => {
      if (reset) {
        setSelected(initSelect)
      }
    }, [reset])

    useEffect(() => {
      const closeSelect = (e: MouseEvent) => {
        if (e.composedPath()[1] !== selectRef.current && isOpen) {
          setIsOpen(false)
        }
      }

      document.body.addEventListener('click', closeSelect)

      return () => document.body.removeEventListener('click', closeSelect)
    }, [isOpen])

    return (
      <div ref={selectRef} className={s.select} onClick={handleToggleSelect}>
        <p className={`${s.selected} ${isOpen ? s.opened : ''}`}>{selected}</p>
        <ul className={`${s.options} ${isOpen ? '' : s.closed}`}>
          {options.map((el, i) => {
            return (
              <li key={i} onClick={e => handleClickOption(e, el.id)} className={s.option}>
                {el.label}
              </li>
            )
          })}
        </ul>
      </div>
    )
  }
)
