import { FC, memo, useState } from 'react'

import { Portal } from '@mui/base'
import { useParams } from 'react-router-dom'

import { isMyPack } from '../../../app/selectors'
import { PackActionsMenu } from '../../../features/packs/PackActionsMenu/PackActionsMenu'
import { useAppSelector } from '../../../hooks/reduxHooks'
import { CustomButton } from '../CustomButton/CustomButton'
import { FiltersComponent } from '../FiltersComponent/FiltersComponent'

import s from './PageTitleBlock.module.scss'

type PageTitleBlockType = {
  linkToPacks?: boolean
  button: string
  packDeckCover?: string | null
  title: string
  buttonClick: () => void
}

export const PageTitleBlock: FC<PageTitleBlockType> = memo(
  ({ linkToPacks, button, title, buttonClick, packDeckCover }) => {
    const { id } = useParams()
    const cardsTotal = useAppSelector(state => state.cards.cardsData.cardsTotalCount)
    const isItMyPack = useAppSelector(isMyPack)
    const [isMenuOpen, setIsMenuOpen] = useState(false)
    const [isFilterOpen, setIsFilterOpen] = useState(false)
    const portalStyle = isMenuOpen || isFilterOpen ? s.portal : ''

    const handleClose = () => {
      setIsMenuOpen(false)
      setIsFilterOpen(false)
      document.body.style.overflow = 'unset'
    }

    const handleClickFilter = () => {
      setIsFilterOpen(true)
      document.body.style.overflow = 'hidden'
    }

    return (
      <>
        <Portal>
          <div className={portalStyle} onClick={handleClose}></div>
        </Portal>

        <div className={s.pageTitleBlockContainer}>
          {/* {linkToPacks && <BackToPacks />} */}
          <div className={s.pageTitleBlock__titleBlock}>
            <h2 className={s.pageTitleBlock__title}>{title}</h2>
            {isItMyPack && (
              <PackActionsMenu packID={id as string} packName={title} packIsEmpty={!!cardsTotal} />
            )}

            {button && (
              <>
                <div className={s.filterButtonContainer}>
                  {linkToPacks ? (
                    <FiltersComponent forCards isShown={isFilterOpen} />
                  ) : (
                    <FiltersComponent isShown={isFilterOpen} />
                  )}
                  <CustomButton className={s.filterButton} onClick={handleClickFilter}>
                    <p>Add filter</p>
                  </CustomButton>
                </div>

                <CustomButton
                  className={button === 'Learn to pack' ? s.learnButton : s.addButton}
                  onClick={buttonClick}
                >
                  <p>{button}</p>
                </CustomButton>
              </>
            )}
          </div>
        </div>
      </>
    )
  }
)
